export default class BibleService {
    getMorphology(db, code) {
        return db.exec(
            `SELECT * FROM tehmc 
    where code = ?`,
            [code]);
    }

    getBookChapVerse(db, book, from_chapter, from_verse) {
        return db.exec(
            `SELECT * FROM totht 
    where book = ? 
    and chapter >= ? 
    and chapter <= ?
    and verse >= ? 
    and verse <= ? 
    order by 
    book ASC, 
    chapter ASC, 
    verse ASC, 
    word ASC`,
            [book, from_chapter, from_chapter, from_verse, from_verse]);
    }
    json_extension(_records) {
        var _json = [];
        var _columns = _records[0].columns
        var _values = _records[0].values
        for (var i = 0; i < _values.length; i++) {
            //console.log(_values[i]);
            var _row_json = {};
            var _row = _values[i];
            for (var k = 0; k < _row.length; k++) {
                _row_json[_columns[k]] = _row[k]
            }
            _json.push(_row_json)
        }
        return _json
    }
    async getBible(db, book, chapter, verse) {
        let _data = this.getBookChapVerse(db, book, chapter, chapter, verse, verse);
        let res = {};
        res.data = this.json_extension(_data);
        for (let i = 0; i < res.data.length; i++) {
            let morph = res.data[i].morph.split('/');
            res.data[i].morphology = [];
            for (let j = 0; j < morph.length; j++) {
                let morphCode = morph[j];
                if (!morphCode.startsWith('H')) {
                    morphCode = 'H' + morphCode;
                }
                _data = this.getMorphology(db, `${morphCode}`);
                let resMorph = {};
                resMorph.data = this.json_extension(_data);
                res.data[i].morphology.push(resMorph.data[0]);
            }

            res.data[i].clean = res.data[i].hebrew_pointed.replace(/[\u0591-\u05C7]/g, "").trim();
        }
        return res;

    }
}
